.main {
    padding-bottom: 60px; 
    ul {
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }
    h1 {
        top: 300px;
        background: $cinza;
        color: $branco;
        padding: 5px 0 10px 0;
        position: absolute;
        text-align: center; 
        z-index: 10;
        width: 100%;
        height: 50px;
        padding-top: 15px;
        opacity: 0.8;
    }
}
.noticias{
    min-height: 374px;
    max-height: 374px;
    overflow-y: scroll;
    h2 {
        border-top: 3px solid $primary;
        padding: 12px 12px 5px 10px;
        height: 32px;
        background: $bege-claro;
        text-transform: uppercase;
        @include tipo-1(18);
        color: $primary;
        position: relative;
    }
    li {
        @include tipo-1(18);
        line-height: em(35, 18);
        padding: 10px 10px 10px 20px;
        border-bottom: 1px solid $bege-claro;
    }

    a {
        text-decoration: none;
        color: $primary
    }
    a:hover {
        text-decoration: none;
        color: $secondary
    }
}

.rslides {
    position: relative;
    list-style: none;
    overflow: hidden;
    width: 100%;
    padding: 0;
    margin: 0;
    }
  
  .rslides li {
    -webkit-backface-visibility: hidden;
    position: absolute;
    display: none;
    width: 100%;
    left: 0;
    top: 0;
    }
  
  .rslides li:first-child {
    position: relative;
    display: block;
    float: left;
    }
  
  .rslides img {
    display: block;
    height: auto;
    float: left;
    width: 100%;
    border: 0;
    }